import React from 'react';
import './AdsensePositions.less';
import { Row, Col, Button, Skeleton } from 'antd';
import { Adsense } from '@app/machine-news';
import { withNamespaces } from '@client/i18n';

interface Props {
  adsenseInfo: Adsense;
  selectedAdsChange: (position: number) => void;
  t: (key: string) => string;
}
const AdsensePositions = (props: Props) => {
  const translate = props.t;

  const buttonType = (position: number) => {
    return props.adsenseInfo.position === position ? 'primary' : 'default';
  };

  return (
    <div>
      <h2 className='adsense-title'>
        {translate('admin-adsense:adsense-position')}
      </h2>
      <div className='adsense-positions'>
        <Row className='adsense-positions-header'>
          <Col xs={24}>
            <div className='text-center'>Timmay.vn</div>
          </Col>
        </Row>

        <Row type='flex' justify='space-between' className='adsense-positions-main'>
          <Col xs={5} className='left'>
            <Button onClick={() => props.selectedAdsChange(1)} type={buttonType(1)} block={true} className='left-1'>1</Button>
            <Button onClick={() => props.selectedAdsChange(2)} type={buttonType(2)} block={true} className='left-2'>2</Button>
          </Col>
          <Col xs={14}>
            <Skeleton title={false} paragraph={{
              rows: 12,
              width: ['100%'],
            }} />
          </Col>
          <Col xs={5} className='right'>
            <Button onClick={() => props.selectedAdsChange(3)} type={buttonType(3)} block={true} className='right-1'>3</Button>
            <Button onClick={() => props.selectedAdsChange(4)} type={buttonType(4)} block={true} className='right-2'>4</Button>
            <Button onClick={() => props.selectedAdsChange(5)} type={buttonType(5)} block={true} className='right-3'>5</Button>
          </Col>
        </Row>
      </div>
    </div>
  );
};

export default withNamespaces('admin-adsense')(AdsensePositions);
